@import url(libs/slick.css);
@font-face {
  font-family: "Gothic";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/CenturyGothic.eot");
  src: url("../fonts/CenturyGothic.eot?#iefix") format("embedded-opentype"), url("../fonts/CenturyGothic.woff") format("woff"), url("../fonts/CenturyGothic.ttf") format("truetype");
}

@font-face {
  font-family: "Gothic-Bold";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/CenturyGothic-Bold.eot");
  src: url("../fonts/CenturyGothic-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/CenturyGothic-Bold.woff") format("woff"), url("../fonts/CenturyGothic-Bold.ttf") format("truetype");
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

body {
  font-family: 'Gothic';
  font-size: 14px;
}

.title-lg {
  font-size: 48px;
  color: #ffffff;
  font-family: 'Gothic-Bold';
}

.bold {
  font-family: 'Gothic-Bold';
}

.header {
  background: rgba(112, 182, 44, 0.9);
  font-family: 'Gothic-Bold';
}

.container {
  max-width: 1280px;
  padding: 0 50px;
  margin: 0 auto;
  position: relative;
}

.header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
}

.tel {
  padding: 13px 30px;
  background: linear-gradient(360deg, #003820 0%, #56A33E 120%);
  opacity: 0.8;
  border-radius: 33px;
}

.tel__link {
  display: block;
  text-decoration: none;
  color: #E4F3DD;
}

.menu {
  display: flex;
}

.menu__item {
  margin-right: 15px;
}

.menu__link {
  font-family: 'Gothic-Bold';
  color: #ffffff;
  text-decoration: none;
}

.lang {
  display: flex;
  flex-direction: column;
}

.lang__item {
  display: block;
  color: #FFFFFF;
  font-size: 12px;
  line-height: 1.25;
  text-decoration: none;
  color: #FFFFFF;
}

.lang__item:not(:last-child) {
  margin-bottom: 7px;
}

.one {
  position: relative;
  height: 640px;
  overflow: hidden;
}

.slider-one__info {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: url("../img/Frame.svg") no-repeat;
}

.slider-one__info-in {
  padding-top: 200px;
  max-width: 496px;
}

.slider-one__text {
  margin: 40px 0;
  font-size: 18px;
  color: #E4F3DD;
  max-width: 300px;
}

.slider-one__item img {
  display: block;
  width: 100%;
  object-fit: cover;
  height: 640px;
}

.slider-one__btns {
  display: flex;
  cursor: pointer;
}

.slider-one__btns img {
  display: block;
}

.slider-one__btn {
  width: 120px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.slider-one__btn-prev {
  transition: background-image all 2s;
}

.slider-one__btn-prev:hover {
  background-image: url("../img/Vector.svg");
}

.slider-one__btn-next {
  transform: rotate(-180deg);
}

.slider-one__btn-next:hover {
  background-image: url("../img/Vector.svg");
}
